<template>
  <div class="login">
    <div v-if="owner === $store.state.addr">
      <h3 ></h3>
      <div v-if="flag">
         {{ $store.state.nickname }}<br>
         {{ $store.state.addr }} <br>
         {{ totalSupply }} <br><br><br>

          {{ price }} 1 = {{ price }} <br>
         :
          <b-input
                    class="mb-2 mr-sm-2 mb-sm-0 user"
                    placeholder="" v-model="newPrice"
            ></b-input>
            <div class="loginbtn">
              <b-button variant="outline-primary" @click="setPrice"> </b-button>
            </div>
            <br><br>

             <br>
            <b-input
                    class="mb-2 mr-sm-2 mb-sm-0 user"
                    placeholder="" v-model="withdrawAmount"
            ></b-input>
            <div class="loginbtn">
              <b-button variant="outline-primary" @click="withdrawLark"> </b-button>
            </div>

      </div>
      <!---->
      <transition name="fade">
        <loading v-if="isLoading"></loading>
      </transition>
    </div>
  </div>
</template>

<script>
  import Loading from './Loading'
  export default {
    name: "Admin",
    data() {
      return {
        isLoading: false,
        flag: false,
        addAmount: '',
        price:'',
        newPrice: '',
        totalSupply: '',
        withdrawAmount: ''
      }
    },
    methods: {
      async setPrice() {
        // 1eth = 68000 els
        this.flag = false
        this.isLoading = true
        let addr = this.$store.state.addr
        let instance = this.$store.state.exchangeinstance
        await instance.setPrice(this.newPrice, {from: addr, gasPrice: this.gasPrice})
        await this.getBalance()
        await this.getExchangeDone()
        this.newPrice = ''
        this.isLoading = false
        this.flag = true
      },
      async getBalance(){
        let instance = this.$store.state.walletinstance
        let b = await instance.totalSupply()
        this.totalSupply = this.weiToEth(b)
      },
      async getExchangeDone(){
        let instance2 = this.$store.state.exchangeinstance
        //
        let a5 = await instance2.getMessage()
        this.price = a5[1]
      },
      async withdrawLark(){
        this.flag = false
        this.isLoading = true
        let addr = this.$store.state.addr
        let instance = this.$store.state.exchangeinstance
        let amount2 = this.web3.utils.toWei(this.withdrawAmount, 'ether')
        await instance.withdrawLark(amount2, {from: addr, gasPrice: this.gasPrice})
        await this.getBalance()
        await this.getExchangeDone()
        this.withdrawAmount = ''
        this.isLoading = false
        this.flag = true
      },
    },

    components: {
        Loading
      },
    mounted() {
      let that = this
      let instance = this.$store.state.walletinstance
      async  function main(){
        if(Object.keys(instance).length === 0){
          //, instance
          // console.log(888, "instance")
          await that.getWalletInstance()
          await that.getExchangeInstance()
          await that.getBalance()
          await that.getExchangeDone()
          that.isLoading = false
          that.flag = true
        } else{
          // console.log(22333, "")
          await that.getBalance()
          await that.getExchangeDone()
          that.isLoading = false
          that.flag = true
        }
      }
      main()
    },

    }
</script>

