  .login{
    margin: 4rem 4rem 4rem 15rem;
  }
  .user{
    margin-top: 1rem;
    width: 30rem;
  }
  .user2{
    display: inline;
    width: 8rem;
  }

  .exchange{
    width: 30rem;
  }
  .loginbtn{
    margin-top: 0.5rem;
  }
