<template>
  <div id="maintop">
    <div  v-if="flag">
    <b-input
      class="mb-2 mr-sm-2 mb-sm-0 title"
      placeholder="" v-model="name"
    ></b-input>

      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 title"
              :class="checkBonusFlag ? 'isok': 'isfalse'"
              placeholder="" v-model="bonus"
              @keyup="checkBonus"
      ></b-input>
      <strong style="color: rgba(240,56,6,0.75)"> {{ bonus/2 }} </strong>

      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 content"
              placeholder=""  v-model="contact"
      ></b-input>

      
    <div id="main">
          <mavon-editor v-model="content" ref="md" @imgAdd="uploadimg" style="height: 100%"/>
    </div>

      <br>
      
      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 hour"
              :class="checkHourFlag ? 'isok': 'isfalse'"
              placeholder="" v-model="hour"
              @keyup="checkHour"
      ></b-input>
      <b-input
              class="mb-2 mr-sm-2 mb-sm-0 hour"
              :class="checkMinuteFlag ? 'isok': 'isfalse'"
              placeholder="" v-model="minute"
              @keyup="checkMinute"
      ></b-input>
      <br>
       0.5%<br>

    <div v-if="isApprove" >
      <div v-if="approveFlag">
      <b-button variant="outline-primary" @click="letapprove" class="btn"> </b-button>
      <span style="color: gray;padding-left: 1rem;"></span><br><br>
      </div>
      <div  v-else style="margin-top: 0.8rem;margin-bottom: 1.5rem">
        
      </div>
    </div>
    <div class="btn" v-else>
      <div v-if="checkBonusFlag && checkHourFlag && checkMinuteFlag">
          <b-button variant="outline-primary" @click="post"></b-button><br>
      </div>
      <div v-else></div>
      <br>
    </div>

    </div>
    <!---->
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>

  </div>
</template>

<script>
  import Loading from './Loading'
    export default {
        name: "PostBounty",
      data() {
        return {
          db:{},
          content: '',
          name: '',
          contact: '',
          bonus: '',
          checkBonusFlag: true,
          hour: '',
          minute: '',
          checkHourFlag: true,
          checkMinuteFlag: true,
          isLoading: false,
          flag: false,
          approveFlag: true
        }
      },

      methods: {
        async letapprove(){
          this.approveFlag = false
          await this.approve()
          await this.getBalanceAndAllowance()
        },
        checkBonus(){
          let reg = /^\d+(\.\d+)?$/
          let res = reg.test(this.bonus)
          this.checkBonusFlag = res
        },
        checkHour(){
          let reg = /^[0-9]+$/
          let res = reg.test(this.hour)
          this.checkHourFlag = res
        },
        checkMinute(){
          let reg = /^[0-9]+$/
          let res = reg.test(this.minute)
          this.checkMinuteFlag = res
        },
        async post(){
            await this.unlockWallet()
            if(this.name !== '' && this.contact !== '' && this.content !== ''){
              if(parseFloat(this.$store.state.balanceOf) >= parseFloat(this.bonus * 1.5) ){
                this.flag = false
                this.isLoading = true
                let nickname = this.$store.state.nickname
                let instance = this.$store.state.bountyinstance
                let addr = this.$store.state.addr
                let name= this.name
                let taskNumber = this.getNum()
                let bonus= this.bonus
                let contact = this.contact
                let content = this.content
                let hour = this.hour
                let minute = this.minute
                let created = this.getTimeStamp()
                let value = this.web3.utils.toWei(bonus, 'ether')
                let task = {
                  "name": name,
                  "nickname": nickname,
                  "taskNumber": taskNumber,
                  "contact": contact,
                  "content": content,
                  'bonus': value,
                  'created': created
                }
                //ipfs
                let con = Buffer.from(JSON.stringify(task))
                let res = await this.ipfs.add(con)
                let hash = res[0].hash
                await instance.setTask(hash, value, hour, minute, {from: addr, gasPrice: this.gasPrice})
                await this.getBalanceAndAllowance()
                //vuex
                this.$store.commit('saveBountys', [])
                this.$router.push({path:'/'})

              }else{
                alert("")
              }

            }else{
              alert("")
            }
          },
        uploadimg(pos, $file){
          this.imgAdd(pos, $file)
        },

      },

      computed: {
        isApprove() {
          return  parseFloat(this.$store.state.allowance) < parseFloat(this.bonus)
        }
      },

      components: {
        Loading
      },

      mounted() {
        let that = this
        let instance = this.$store.state.bountyinstance
        async  function main(){
          if(Object.keys(instance).length === 0){
            //, instance
            // console.log(888, "instance")
            let instance = await that.getBountyInstance()
            await that.getTasks(instance)
            that.isLoading = false
            that.flag = true
          } else{
            if(that.$store.state.bountys == false){
              // console.log(6647, "")
              //
              await that.getTasks(instance)
              that.isLoading = false
              that.flag = true
            } else{
              // console.log(22333, "")
              that.isLoading = false
              that.flag = true
            }
          }

          //
          if(that.$store.state.allowance === ''){
            // console.log(111, "")
            await that.getBalanceAndAllowance()
          }else{
            // console.log(222, "")
          }

        }
        main()
      },
    }
</script>

